html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.container {
  width: 960px;
  margin: 0 auto;
  padding: 0px 10px;
  background-color: #fff;
}

.container:after {
  content: "";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1200px) {
  .container {
    width: 970px;
  }
}

@media only screen and (max-width: 993px) {
  .container {
    width: 750px;
  }
}

@media only screen and (max-width: 769px) {
  .container {
    width: 100%;
  }
}

.header {
  width: 100%;
  padding: 20px 0px;
  position: absolute;
  top: 0px;
  z-index: 9999;
}

.logo {
  display: inline-block;
}

.nav {
  float: right;
  margin-top: 15px;
}

.nav:after {
  content: "";
  display: table;
  clear: both;
}

@media only screen and (max-width: 769px) {
  .nav {
    display: none;
    float: none;
    text-align: center;
  }
}

.nav__item {
  display: inline-block;
  margin-left: 28px;
}

@media only screen and (max-width: 769px) {
  .nav__item {
    display: block;
    margin-top: 15px;
  }
}

.nav__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #b8b8b8;
  font-size: 16px;
  font-family: 'bebas';
}

.nav__link:hover {
  color: #e8603c;
}

.responsive-menu {
  width: 35px;
  height: 35px;
  position: relative;
  cursor: pointer;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

@media only screen and (max-width: 769px) {
  .responsive-menu {
    display: inline-block;
    position: absolute;
    top: 24px;
    right: 35px;
  }
}

.responsive-menu span {
  display: block;
  position: absolute;
  height: 5px;
  width: 100%;
  background: #e75f3b;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

.responsive-menu span:nth-child(1) {
  top: 0px;
}

.responsive-menu span:nth-child(2) {
  top: 13px;
}

.responsive-menu span:nth-child(3) {
  top: 13px;
}

.responsive-menu span:nth-child(4) {
  top: 26px;
}

.responsive-menu.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.responsive-menu.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.responsive-menu.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.responsive-menu.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

@media only screen and (max-width: 769px) {
  .responsive {
    display: block;
  }
}

.page-title {
  background-color: rgba(232, 96, 60, 0.5);
  margin: 80px 0px 24px 0px;
}

.page-title__text {
  color: #fff;
  font-size: 24px;
  text-transform: uppercase;
  background-color: #e8603c;
  padding: 19px;
}

@media only screen and (max-width: 769px) {
  .page-title__text {
    text-align: center;
  }
}

.posts-list {
  float: left;
  width: 71%;
}

.posts-list:after {
  content: "";
  display: table;
  clear: both;
}

@media only screen and (max-width: 993px) {
  .posts-list {
    width: 67%;
  }
}

@media only screen and (max-width: 769px) {
  .posts-list {
    width: 100%;
  }
}

.post {
  position: relative;
}

.post__icon {
  display: inline-block;
  background-color: #e8603c;
  padding: 22px;
  position: absolute;
  left: -82px;
}

@media only screen and (max-width: 1130px) {
  .post__icon {
    top: 10px;
    left: 10px;
  }
}

.post__img {
  margin-bottom: 25px;
}

.post__metadata {
  float: left;
  width: 200px;
}

.post__content {
  margin-left: 200px;
  color: #414141;
  font-size: 12px;
}

.post__content p {
  margin-bottom: 25px;
}

.post__title {
  color: #e8603c;
  font-size: 18px;
  text-transform: uppercase;
}

.post__description:after {
  content: "";
  display: table;
  clear: both;
}

.post__img_size {
  width: 100%;
}

.metadata__title {
  color: #e8603c;
  font-size: 12px;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.metadata__text {
  color: #c8c8c8;
  font-size: 12px;
  text-decoration: none;
  margin-right: 7px;
}

.metadata__text_border {
  border-right: 1px solid #c8c8c8;
  padding-right: 7px;
}

.metadata__item {
  margin-bottom: 15px;
}

.post__line {
  height: 14px;
  background: url("../img/line.png");
  margin: 25px 0px 55px 0px;
}

.sidebar {
  float: right;
  width: 222px;
}

.sidebar__item {
  margin-bottom: 39px;
}

@media only screen and (max-width: 769px) {
  .sidebar__item {
    text-align: center;
  }
}

.sidebar__title {
  color: #e8603c;
  font-size: 14px;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.sidebar__text {
  color: #464646;
  font-size: 12px;
  line-height: 1.5;
  text-decoration: none;
}

.sidebar__img {
  margin: 0px 2px 5px 0px;
}

@media only screen and (max-width: 769px) {
  .sidebar {
    float: none;
    width: 100%;
  }
}

.sidebar__title_wrap {
  background: url("../img/line.png");
}
